.container {
  height: 100vh;
  display: flex;
}

.front__img-wrapper {
  width: 42.96875%;
  overflow: hidden;
}

.front__img {
  height: 100%;
}

.front__right {
  width: 57.03125%;
  overflow: hidden;
}

.header {
  background-color: #131313;
  width: 100%;
  display: flex;
  height: 25.8335%;
}

.header .logo-wrapper {
  background: #21423D;
  width: 279px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .logo-wrapper .logo-img {
  width: 100px;
  height: auto;
}

.header .header-nav {
  display: flex;
  align-items: center;
  width: 74.88584474885845%;
}

.header .header-nav .nav__list {
  list-style: none;
  display: flex;
}

.header .header-nav .nav__list .nav__list-item {
  margin-right: 25px;
}

.header .header-nav .nav__list .nav__list-item .nav__list-link {
  font-family: 'Roboto';
  color: #E7B69B;
  text-decoration: none;
  font-size: 16px;
}

.header .header-nav .nav__list .nav__list-item .nav__list-link:hover {
  opacity: 0.6;
}

.front__info {
  display: flex;
  height: 25.8335%;
  justify-content: space-between;
  background-color: #131313;
  color: #E7B69B;
}

.front__info .info__deals {
  display: flex;
  padding: 50px 40px;
  width: 74.88584474885845%;
  justify-content: space-between;
}

.front__info .info__deals .info__deals-item {
  display: flex;
  flex-direction: column;
}

.front__info .info__deals .info__deals-item .info__deals-title {
  margin-bottom: 40px;
}

.front__info .info__deals .info__deals-item .info__deals-text {
  font-size: 20px;
  letter-spacing: 1.2px;
  font-weight: 500;
}

.btn {
  background: transparent;
  padding: 4px 15px;
}

.info__deals-btn {
  border: 2px solid #E7B69B;
  color: #E7B69B;
  margin-bottom: 27px;
  transition: all 0.3s;
}

.info__deals-btn:hover {
  color: #131313;
  background-color: #E7B69B;
}

.front__map {
  height: 48.333%;
}

.grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 5px;
  background-color: #131313;
}

.grid-item {
  border: 1px solid black;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.grid-item-text {
  background-color: #21423D;
  padding: 50px 40px;
}

.grid-item-text .grid-title {
  font-size: 30px;
  color: #E7B69B;
  font-weight: 500;
}

.grid-item-text .grid-text {
  padding-top: 30px;
  color: #E7B69B;
}

.grid-item-text--adv {
  text-align: center;
}

.grid-item:nth-child(1) {
  grid-area: 1/1/5/4;
}

.grid-item:nth-child(2) {
  grid-area: 1/4/2/8;
}

.grid-item:nth-child(3) {
  grid-area: 1/8/1/-1;
}

.grid-item:nth-child(4) {
  grid-area: 2/4/6/9;
}

.grid-item:nth-child(5) {
  grid-area: 2/-3/7/-1;
}

.grid-item:nth-child(6) {
  grid-area: 5/1/-3/4;
}

.grid-item:nth-child(7) {
  grid-area: -6/4/-1/-3;
}

.grid-item:nth-child(8) {
  grid-area: -5/-3/-1/-1;
}

.grid-item:nth-child(9) {
  grid-area: -3/1/-1/3;
}

.grid-item:nth-child(10) {
  grid-area: -3/3/-1/4;
}

.location {
  background-color: #131313;
  height: 70vh;
  display: flex;
  flex-direction: column;
}

.inner-container {
  width: 1200px;
  margin: 0 auto;
  padding: 80px 30px 50px 30px;
  color: #E7B69B;
}

.inner__text {
  width: 50%;
  margin-left: auto;
}

.map {
  width: 100%;
  height: 100%;
}

.appartment__title {
  color: #131313;
}

.appartament {
  display: flex;
  flex-direction: column;
}

.appartments__slider {
  display: flex;
  overflow: hidden;
}

.appartments__slider .owl-stage {
  display: flex;
}

.slide-item {
  display: flex;
  justify-content: space-between;
  border: 2px solid #131313;
  width: 600px !important;
  height: 300px;
  background-color: #fff;
  margin: 50px;
}

.slide__right {
  padding: 25px;
  width: 50%;
  display: flex;
}

.slide__right img {
  width: 100%;
  align-self: flex-end;
}

.slide__left {
  padding: 25px 0 25px 25px;
  margin-top: auto;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.slide__btn {
  border: 2px solid #131313;
  color: #131313;
  transition: all 0.3s;
}

.slide__btn:hover {
  background-color: #131313;
  color: #fff;
}

.slide__dsc-line {
  border-bottom: 1px solid #131313;
  display: flex;
  padding-bottom: 5px;
  margin-bottom: 5px;
}

.slide__desc-item:first-child {
  width: 50%;
}

.slide__desc {
  margin: auto 0;
}

.slide__btn {
  margin-top: auto;
}

.appartments__filter-item {
  border: none;
  background: transparent;
  padding-left: 5px;
  margin-right: 3px;
  border-right: 1px solid #131313;
  transition: all 0.3s;
}

.appartments__filter-item:first-child {
  padding-left: 0;
}

.appartments__filter-item:hover {
  opacity: 0.6;
}

.appartments__filter-item.active {
  opacity: 0.6;
}

.appartament .inner-container {
  padding-bottom: 0;
}
